<template>
  <div>
    <Sidebar></Sidebar>
      <div class="object_container">
        <h1 class="mt-3">
          Yangi obyekt yaratish
        </h1>
        <div class="create_objects">
            <div class="create_form">
              <form @submit.prevent="create_main()">
                <div class="row">
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Obyekt turi</label>
                      <div class="create_input">
                        <select class="form-control" v-model="object_type_id" name="cars" id="cars" >
                          <option value="Arxeologiya yodgorliklari">Arxeologiya yodgorliklari</option>
                          <option value="Monumental san'at yodgorliklari">Monumental san'at yodgorliklari</option>
                          <option value="Diqqatga sazovor joylar">Diqqatga sazovor joylar</option>
                          <option value="Arxitektura yodgorliklari">Arxitektura yodgorliklari</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Obyektning maqsad va vazifalari</label>
                      <div class="create_input">

                        <select class="form-control" v-model="object_goal_id" name="cars" id="cars" >
                          <option value="Madaniy">Madaniy</option>
                          <option value="Marifiy">Marifiy</option>
                          <option value="Turistik">Turistik</option>
                          <option value="Ekskursion">Ekskursion</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Obyekt nomi</label>
                      <div class="create_input">
                        <input type="text" v-model="name">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Obyekt tasnifi</label>
                      <div class="create_input">
                        <select class="form-control" v-model="object_desk_id" name="cars" id="cars" >
                          <option value="Jahon">Jahon</option>
                          <option value="Respublika">Respublika</option>
                          <option value="Mahalliy">Mahalliy</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Obyekt tasnifi</label>
                      <div class="create_input">
                        <select class="form-control" v-model="object_desk1_id" name="cars" id="cars" >
                          <option value="Turar">Turar</option>
                          <option value="Noturar">Noturar</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Obyekt davri</label>
                      <div class="create_input">
                        <input v-model="object_age" type="text">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Viloyat</label>
                      <select class="form-control" name="cars" id="cars" v-model="objectItem" @change="getregion()">
                        <option v-for="(item,index) in options1" :key="index" :value="item">{{item.name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Tuman</label>
                      <select class="form-control" name="cars" id="cars" v-model="objectItem1" @change="getkvartals()">
                        <option v-for="(item,index) in options2" :key="index" :value="item">{{item.name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Manzili</label>
                      <select class="form-control" name="cars" id="cars" v-model="objectItem2" @change="getkvartals()">
                        <option v-for="(item,index) in options3" :key="index" :value="item">{{item.name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Kadastr raqami</label>
                      <div class="create_input">
                        <input v-model="k_number" type="text">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Kenglik</label>
                      <div class="create_input">
                        <input v-model="lat" type="text">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Uzunligi</label>
                      <div class="create_input">
                        <input type="text" v-model="lng">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Ko'chmas mulk huquqi</label>
                      <div class="create_input">
                        <select class="form-control" v-model="author" name="cars" id="cars" >
                          <option value="Operativ boshqaruv">Operativ boshqaruv</option>
                          <option value="Xojalik jamiyat mulki">Xojalik jamiyat mulki</option>
                          <option value="xususiy mulk"> xususiy mulk</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Balansda saqlovchi
                        / tasarruf etuvchi nomi to'liq</label>
                      <div class="create_input">
                        <input v-model="Rented" type="text">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Umumiy yer maydoni</label>
                      <div class="create_input">
                        <input type="text" v-model="area">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Bino maydoni</label>
                      <div class="create_input">
                        <input type="text" v-model="surface">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Gaz (bor/yo'q)</label>
                      <div class="create_input">
                        <select class="form-control" v-model="gas" name="cars" id="cars" >
                          <option value="yes">Bor</option>
                          <option value="no">Yo'q</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Suv(bor/yo'q)</label>
                      <div class="create_input">
                        <select class="form-control" v-model="water" name="cars" id="cars" >
                          <option value="yes">Bor</option>
                          <option value="no">Yo'q</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Elektr(bor/yo'q)</label>
                      <div class="create_input">
                        <select class="form-control" v-model="electricity" name="cars" id="cars" >
                          <option value="yes">Bor</option>
                          <option value="no">Yo'q</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Ijaraga berilgan (bor/yo'q)</label>
                      <div class="create_input">
                        <select class="form-control" v-model="marked" name="cars" id="cars" >
                          <option value="yes">Bor</option>
                          <option value="no">Yo'q</option>
                        </select>
                      </div>
                    </div>
                  </div>

                  <div class="col-xl-3">
                    <div>
                      <label for="#">Ijara shartnoma soni</label>
                      <div class="create_input">
                        <input type="text" v-model="contracts">
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <div>
                      <label for="#">Muhofaza tegrasi belgilanganligi</label>
                      <div class="create_input">
                        <select class="form-control" v-model="surface" name="cars" id="cars" >
                          <option value="Belgilangan">Belgilangan</option>
                          <option value="Belgilanmagan">Belgilanmagan</option>
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <label for="#">Shartnoma</label>
                    <div class="create_input">

                      <input type="file" class="form-control"
                             multiple
                             @change="trySubmitFile"
                             accept="image/*"
                             name="datafiles"
                             ref="datafiles"
                      >
                    </div>
                  </div>
                  <div class="col-xl-3">
                    <label for="#">Rasmlar</label>
                    <div class="create_input">
                      <input type="file" class="form-control"
                             multiple
                             @change="trySubmitFile1"
                             accept="image/*"
                             name="datafiless"
                             ref="datafiless"
                      >
                    </div>
                  </div>
                  <div class="col-xl-12">
                    <button class="btn w-100 btn-primary">
                      Saqlash
                    </button>
                  </div>
                </div>
              </form>
            </div>
         </div>
      </div>
    <Navbar></Navbar>
  </div>
</template>


<script>
import Sidebar from "@/components/Sidebar.vue";
import Navbar from "@/components/Navbar.vue";
import Destricts from '@/Json/districts.json'
import Regions from '@/Json/regions.json'
import Kvartals from '@/Json/kvartals.json'
export default {
  components:{
    Navbar,
    Sidebar,
  },
  data() {
    return {
      name:'',
      object_type_id:'',
      object_goal_id:'',
      object_desk_id:'',
      object_desk1_id:'',
      images:'',
      contracts_file:'',
      object_age:'',
      region:'',
      district:'',
      address:'',
      k_number:'',
      author:'',
      lat:'',
      lng:'',
      surface:'',
      area:'',
      gas:'',
      water:'',
      electricity:'',
      Rented:'',
      contracts:'',
      marked:'',


      myImages:[],
      imageUrl:[],
      files_name:[],

      myImages1:[],
      imageUrl1:[],
      files_name1:[],



      perPage: 3,
      currentPage: 1,
      items: [
        { id: 1, first_name: 'Fred', last_name: 'Flintstone' },
        { id: 2, first_name: 'Wilma', last_name: 'Flintstone' },
        { id: 3, first_name: 'Barney', last_name: 'Rubble' },
        { id: 4, first_name: 'Betty', last_name: 'Rubble' },
        { id: 5, first_name: 'Pebbles', last_name: 'Flintstone' },
        { id: 6, first_name: 'Bamm Bamm', last_name: 'Rubble' },
        { id: 7, first_name: 'The Great', last_name: 'Gazzoo' },
        { id: 8, first_name: 'Rockhead', last_name: 'Slate' },
        { id: 9, first_name: 'Pearl', last_name: 'Slaghoople' }
      ],

      search:'',
      stats:[],
      sportsman:[],

      selected: [], // Must be an array reference!
      options: [
      ],
      gender:'Erkak',

      district_name:'',
      content:'',
      date:new Date(),
      options1: [
      ],
      options2: [
      ],
      options3: [
      ],
      options4: [
        {
          code:1,
          name:"Hokim sektori"
        },
        {
          code:2,
          name:"Prokuror sektori"
        },
        {
          code:3,
          name:"Ichki Ishlar sektori"
        },
        {
          code:4,
          name:"Davlat Soliq sektori"
        }
      ],
      options5: [
        {
          code:1,
          name:"Workout"
        },
        {
          code:2,
          name:"Stritbol"
        },
        {
          code:3,
          name:"Voleybol"
        },
        {
          code:4,
          name:"Shashka"
        },
        {
          code:5,
          name:"Stol tennis"
        },
        {
          code:6,
          name:"Yengil atletika"
        },
      ],

      options6: [
        {
          code:1,
          name:"7-10"
        },
        {
          code:2,
          name:"11-15"
        },
        {
          code:3,
          name:"16-21"
        },
        {
          code:4,
          name:"22-26"
        },
        {
          code:5,
          name:"27-30"
        },
      ],
      options7:[],

      options8: [
        {
          code:1,
          name:"O'g'il"
        },
        {
          code:2,
          name:"Qiz"
        },
      ],
      objectItem: {},
      objectItem1: {},
      objectItem2: {},
      objectItem3: {},
      objectItem4: {},
      objectItem5: {},
      objectItem6: {},
      objectItem7: {},
      stringItem: '',
      regions: [],
      minDate: '1950',
      maxDate: '2040',
      isFilt:'',
      filTname:'',
      regions_number:''
    }
  },
  mounted() {
    this.city=Regions
    this.region=Destricts
    this.destricts=Kvartals


    Regions.forEach(el=>{
      this.options1.push({
        code:el.id,
        name:el.name
      })
    })

  },

  methods: {

    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    create_main(){
      const form = new FormData();
      form.append("contracts_file", this.myImages[0]);
      form.append("images", this.myImages1[0]);
      form.append("name", this.name);
      form.append("object_type_id", this.object_type_id);
      form.append("object_goal_id", this.object_goal_id);
      form.append("object_desk_id", this.object_desk_id);
      form.append("object_desk1_id", this.object_desk1_id);
      form.append("object_age", this.object_age);
      form.append("region", this.objectItem.code);
      form.append("district", this.objectItem1.code);
      form.append("address", this.objectItem2.code);
      form.append("k_number", this.k_number);
      form.append("author", this.author);
      form.append("lat", this.lat);
      form.append("lng", this.lng);
      form.append("surface", this.surface);
      form.append("area", this.area);
      form.append("gas", this.gas);
      form.append("water", this.water);
      form.append("electricity", this.electricity);
      form.append("Rented", this.Rented);
      form.append("contracts", this.contracts);
      form.append("marked", this.marked);
      form.append("district_name", this.objectItem.name);
      form.append("regions_number", this.objectItem1.name);
      this.$http.post('/objects/',form,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(()=>{
            this.$toast("Ma'lumot qo'shildi", {
              timeout: 2000,
              type:"success"
            });
            this.$router.push('/objects')
          })
          .catch(()=>{
            this.$toast("Xatolik Bomi va kadastr raqami takrorlanishi", {
              timeout: 2000,
              type:"error"
            });
          })
    },
    getregion(){
      console.log(this.objectItem)
      this.options2=[]
      this.options3=[]

      Destricts.filter(tree => tree.parent === this.objectItem.code).forEach(el=>{
        this.options2.push({
          code:el.id,
          name:el.name
        })
      })

    },
    getkvartals(){
      Kvartals.filter(tree => tree.parent === this.objectItem1.code).forEach(el=>{
        this.options3.push({
          code:el.id,
          name:el.name
        })
      })
      if(this.objectItem1.code!=undefined){
        this.filTname=this.filTname+`&region=${this.objectItem1.code}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&city=${this.objectItem.code}&region=${this.objectItem1.code}`
          })
    },
    getSector(){

      if(this.objectItem3.code!=undefined){
        this.filTname=this.filTname+`&sector=${this.objectItem3.name}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&sector=${this.objectItem3.name}`
          })
    },
    getDistrict(){

      if(this.objectItem2.code!=undefined){
        this.filTname=this.filTname+`&district=${this.objectItem2.code}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&city=${this.objectItem.code}&region=${this.objectItem1.code}&sector=${this.objectItem3.name}&district=${this.objectItem2.code}`
          })

    },
    getSport(){

      if(this.objectItem4.code!=undefined){
        this.filTname=this.filTname+`&sport_type=${this.objectItem4.name}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&sport_type=${this.objectItem4.name}`
          })
    },
    searchFilt(){

      if(this.search!=undefined){
        this.filTname=this.filTname+`&name=${this.search}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
          })
    },
    getAge(){

      if(this.objectItem5.code!=undefined){
        this.filTname=this.filTname+`&age=${this.objectItem5.name}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&age=${this.objectItem5.name}`
          })
    },
    getSex(){

      if(this.objectItem7.code!=undefined){
        this.filTname=this.filTname+`&gender=${this.objectItem7.name}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&gender=${this.objectItem7.name}`
          })
    },
    getResult(){

      if(this.objectItem6.code!=undefined){
        this.filTname=this.filTname+`&results__name=${this.objectItem6.name}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
            this.isFilt=`&results__name=${this.objectItem6.name}`
          })
    },
    clear(){
      this.isFilt='',
          this.sportsman=[],
          this.selected= [], // Must be an array reference!
          this.options= [],
          this.gender="O'gil",
          this.name='',
          this.district_name='',
          this.content='',
          this.date=new Date(),
          this.options1= [],
          this.options2= [],
          this.options3= [],
          this.options4= [
            {
              code:1,
              name:"Hokim sektori"
            },
            {
              code:2,
              name:"Prokuror sektori"
            },
            {
              code:3,
              name:"Ichki Ishlar sektori"
            },
            {
              code:4,
              name:"Davlat Soliq sektori"
            }
          ],

          this.options5=[
            {
              code:1,
              name:"Workout"
            },
            {
              code:2,
              name:"Stritbol"
            },
            {
              code:3,
              name:"Voleybol"
            },
            {
              code:4,
              name:"Shashka"
            },
            {
              code:5,
              name:"Stol tennis"
            },
            {
              code:6,
              name:"Yengil atletika"
            },
          ],

          this.options6=[
            {
              code:1,
              name:"7-10"
            },
            {
              code:2,
              name:"11-15"
            },
            {
              code:3,
              name:"16-21"
            },
            {
              code:4,
              name:"22-26"
            },
            {
              code:5,
              name:"27-30"
            },
          ],
          this.options7=[],

          this.options8= [
            {
              code:1,
              name:"O'g'il"
            },
            {
              code:2,
              name:"Qiz"
            },
          ],
          this.objectItem={},
          this.objectItem1={},
          this.objectItem2={},
          this.objectItem3={},
          this.objectItem4={},
          this.objectItem5={},
          this.objectItem6={},
          this.objectItem7={},
          this.stringItem='',
          this.regions=[],
          this.minDate='1950',
          this.maxDate='2040',
          this.city=Regions
      this.region=Destricts
      this.destricts=Kvartals

      this.$axiosDefault.get('v1/api/results/')
          .then(res=>{
            console.log(res.data)
            res.data.forEach(el=>{
              this.options7.push(
                  {
                    code:el.id,
                    name:el.name
                  })
            })
          })
      this.$axiosDefault.get(`v1/api/sportsman?page=${1}`)
          .then(res=>{
            this.sportsman=res.data
          })
      Regions.forEach(el=>{
        this.options1.push({
          code:el.id,
          name:el.name
        })
      })
    }
  }
}
</script>