            {
              code:1,
              name:"7-10"
            },
            {
              code:2,
              name:"11-15"
            },
            {
              code:3,
              name:"16-21"
            },
            {
              code:4,
              name:"22-26"
            },
            {
              code:5,
              name:"27-30"
            },
          ],
          this.options7=[],

          this.options8= [
            {
              code:1,
              name:"O'g'il"
            },
            {
              code:2,
              name:"Qiz"
            },
