          .then(res=>{
            console.log(res.data)
            res.data.forEach(el=>{
              this.options7.push(
                  {
                    code:el.id,
                    name:el.name
                  })
            })
          })
      this.$axiosDefault.get(`v1/api/sportsman?page=${1}`)
          .then(res=>{
            this.sportsman=res.data
          })
      Regions.forEach(el=>{
        this.options1.push({
          code:el.id,
          name:el.name
        })
      })
    },
