<template>
  <div class="login_f">
    <div class="father_login">
      <div class="login_icon">
        <img src="../assets/img/xatlov.png" alt="">
      </div>
      <div class="input_father">
        <div>
          <div class="main_text">
            Kirish
          </div>
          <div class="main_description">
            Madaniy meros obyektlarining monitoring davomida <br>toplangan video, foto materiallarni yagona axborot bazasi
          </div>
          <form @submit.prevent="login()">
            <div class="login_inputs">
              <label for="Email">Email</label>
              <div class="login_input">
                <input type="text" v-model="username">
              </div>
            </div>
            <div class="login_inputs">
              <label for="Email">Password</label>
              <div class="login_input">
                <input type="password"  v-model="password">
              </div>
            </div>
            <div>
              <button class="btn btn-primary w-100 mt-3">Kirish</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <div class="father_right">

    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        username:'',
        password:''
      }
    },
    methods:{
      login(){
        this.$http.post('login/',{
          "username":this.username,
          "password":this.password
        })
            .then(res=>{
              localStorage.setItem('token',res.data.access)
              this.$router.push('/')
            })
            .catch(()=>{
              this.$toast("Login yoki parol xato", {
                timeout: 2000,
                type:"error"
              });
            })
      }
    }
  }
</script>